.wrapper {
  z-index: 10001;
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.gloryhole {
  position: absolute;
  width: 0px;
  height: 0px;
  /* border-radius: 50%; */
  border: 5px solid aliceblue;
  background-color: transparent;
  box-shadow: 0 0 0 10000px rgba(42, 46, 44, 0.9);
  transition: all 1s;
}

.text-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 400px;
}

.desc {
  /* top: 40%;
  left: 50%; */
  font-size: 24px;
  color: white;
}

.button {
  color: lavender;
  font-size: 18px;
  height: 40px;
  width: 150px;
  text-decoration: none;
}
.button:hover {
  color: lightcoral;
}